@tailwind base;
@tailwind components;
@tailwind utilities;

:root {
  --primary: #5b5dff;
  --primary-transparent: rgba(91, 93, 255, 0.1);
  --primary-2: #fde3eb;
  --secondary: #20222a;
  --secondary-2: #292b37;
  --selection: var(--cyan);
  --text-base: #4d515d;
  --p: #4d515d;
  --text-primary: #5b5dff;
  --text-secondary: white;
  --hover: rgba(0, 0, 0, 0.075);
  --hover-1: rgba(91, 93, 255, 0.39);
  --hover-2: rgba(0, 0, 0, 0.25);
  --border: #25272e;
  --cyan: #22b8cf;
  --green: #37b679;
  --red: #da3c3c;
  --purple: #f81ce5;
  --blue: #0070f3;
  --pink: #ff0080;
  --pink-light: #ff379c;
  --magenta: #eb367f;
  --violet: #7928ca;
  --violet-dark: #4c2889;
  --accent-0: #ffffff;
  --accent-1: rgba(11, 10, 16, 0.8);
  --accent-2: rgba(11, 10, 16, 0.6);
  --accent-3: #eceff5;
  --accent-4: rgba(255, 255, 255, 0.8);
  --accent-5: rgba(255, 255, 255, 0.6);
  --accent-6: #e8ecf4;
  --accent-7: rgba(255, 255, 255, 0.64);
  --accent-8: rgba(154, 162, 182, 0.4);
  --accent-9: #030303;
  --font-sans: sfpro, -apple-system, system-ui, BlinkMacSystemFont,
    "Helvetica Neue", "Helvetica", sans-serif;
}

[data-theme="dark"] {
  --primary: #ff487f;
  --primary-transparent: rgba(255, 72, 127, 0.05);
  --primary-2: #fde3eb;
  --secondary: #0c0c0c;
  --secondary-2: #000000;
  --selection: var(--cyan);
  --text-base: #3f3f3f;
  --p: #4d515d;
  --text-primary: #ff487f;
  --text-secondary: white;
  --hover: rgba(0, 0, 0, 0.075);
  --hover-1: rgba(0, 0, 0, 0.1);
  --hover-2: rgba(0, 0, 0, 0.25);
  --border: rgba(255, 255, 255, 0.1);
  --cyan: #22b8cf;
  --green: #37b679;
  --red: #da3c3c;
  --purple: #f81ce5;
  --blue: #0070f3;
  --pink: #ff0080;
  --pink-light: #ff379c;
  --magenta: #eb367f;
  --violet: #7928ca;
  --violet-dark: #4c2889;
  --accent-0: #fff;
  --accent-1: #f4f6fb;
  --accent-2: #eaeaea;
  --accent-3: #eceff5;
  --accent-4: rgba(255, 255, 255, 0.8);
  --accent-5: rgba(255, 255, 255, 0.6);
  --accent-6: #e8ecf4;
  --accent-7: #9aa2b6;
  --accent-8: rgba(154, 162, 182, 0.4);
  --accent-9: #000;
  --font-sans: sfpro, -apple-system, system-ui, BlinkMacSystemFont,
    "Helvetica Neue", "Helvetica", sans-serif;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

html,
body {
  height: 100%;
  box-sizing: border-box;
  touch-action: manipulation;
  font-family: "sfpro";
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--accent-9);
  color: var(--text-primary);
  overscroll-behavior-x: none;
  font-size: 14px;
  scroll-behavior: smooth;
}

body {
  position: relative;
  min-height: 100%;
  margin: 0;
  font-family: "sfpro";
  overflow-x: hidden;
}
html::-webkit-scrollbar,
body::-webkit-scrollbar,
div::-webkit-scrollbar {
  display: none;
}

a {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

.animated {
  animation-duration: 1s;
  animation-fill-mode: both;
  -webkit-animation-duration: 1s;
  -webkit-animation-fill-mode: both;
}

.fadeIn {
  animation-name: fadeIn;
  -webkit-animation-name: fadeIn;
}
.h-screen {
  height: 100vh; /* Fallback for browsers that do not support Custom Properties */
  height: calc(var(--vh, 1vh) * 100);
}

@-webkit-keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

body.loading * {
  transition: none !important;
}

.my-color {
  color: red;
}

.my-font {
  font-family: "Work Sans";
}

.my-root-class {
  background: #030303;
  background-image: radial-gradient(#1f153d, #000000);
  background-repeat: no-repeat;
}

.my-root-class body {
  background: #030303;
  background-image: radial-gradient(#1f153d, #000000);
  background-repeat: no-repeat;
  height: auto;
}

.my-root-class .kcFormCardClass {
  @apply bg-secondary rounded-3xl p-24;
  background-color: rgb(32 34 42 / 60%);
  backdrop-filter: blur(10px);
  -webkit-box-shadow: 0px 0px 44px 1px rgba(32, 34, 42, 0.43);
  -moz-box-shadow: 0px 0px 44px 1px rgba(32, 34, 42, 0.43);
  box-shadow: 0px 0px 44px 1px rgba(32, 34, 42, 0.43);
}

.kcButtonClass {
  @apply bg-primary text-accent-0 cursor-pointer inline-flex
	px-10 py-0 leading-6 transition ease-in-out duration-300
	text-center justify-center uppercase
	border border-transparent items-center text-xs font-bold 
	rounded-xl tracking-wider;
  max-height: 48px;
  line-height: 48px;
  height: 48px;
}

.kcButtonClass:hover {
  @apply text-accent-0 transition ease-in-out duration-300;
  background-color: #e7346a;
  border-color: #e7346a;
}

.kcButtonClass:focus,
.kcButtonClass:active,
.kcButtonClass:focus:active {
  @apply text-accent-0 transition ease-in-out duration-300 outline-none;
  background-color: #e7346a;
  border-color: #e7346a;
}

.kcButtonClass[data-active] {
  @apply bg-primary;
}

.kcLabelClass {
  @apply !mb-2;
}

.primary {
}

.kcInputClass {
  @apply border-0 py-2 px-5 w-full appearance-none transition duration-150 ease-in-out pr-10 text-accent-7 font-bold !h-14 bg-secondary-2 rounded-3xl;
}

.kcInputClass:focus {
  @apply outline-none;
}

.kcInputClass::placeholder {
  font-weight: 600;
}

.form-control:focus {
  border-color: var(--primary);
  outline: 0;
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075),
    0 0 8px rgba(91, 93, 255, 0.6);
}

.kcHeaderWrapperClass {
  @apply text-primary font-bold;
}

.alert {
  @apply rounded-3xl;
}

.alert-error {
  @apply font-bold text-accent-3 font-sfpro bg-secondary-2;
  border: 0;
  background: linear-gradient(
    90deg,
    rgba(255, 2, 81, 0.4) 0%,
    rgb(41, 43, 55) 40%,
    rgb(41, 43, 55) 100%
  );
  border-color: transparent;
}

.alert-success {
  @apply font-bold text-accent-3 font-sfpro bg-secondary-2;
  border: 0;
  background: linear-gradient(
    90deg,
    rgba(41, 124, 84, 0.4) 0%,
    rgb(41, 43, 55) 40%,
    rgb(41, 43, 55) 100%
  );
  border-color: transparent;
}

.alert-error,
.pficon-error-circle-o:before {
  color: #ff0251;
}

.pficon-ok:before {
  color: #00e076;
}

#kc-page-title {
  @apply my-6;
}

.has-error .checkbox,
.has-error .checkbox-inline,
.has-error .control-label,
.has-error .radio,
.has-error .radio-inline,
.has-error.checkbox label,
.has-error.checkbox-inline label,
.has-error.radio label,
.has-error.radio-inline label,
.has-success .checkbox,
.has-success .checkbox-inline,
.has-success .control-label,
.has-success .radio,
.has-success .radio-inline,
.has-success.checkbox label,
.has-success.checkbox-inline label,
.has-success.radio label,
.has-success.radio-inline label,
.has-warning .checkbox,
.has-warning .checkbox-inline,
.has-warning .control-label,
.has-warning .radio,
.has-warning .radio-inline,
.has-warning.checkbox label,
.has-warning.checkbox-inline label,
.has-warning.radio label,
.has-warning.radio-inline label {
  color: #ff0251;
}

.menu {
  position: relative;
  z-index: 50;
  display: flex;
  min-width: 100px;
  flex-direction: column;
  overscroll-behavior: contain;
  border-radius: 0.5rem;
  border-width: 1px;
  border-style: solid;
  border-color: var(--border);
  background-color: rgb(32 34 42 / 60%);
  backdrop-filter: blur(10px);
  padding: 0.5rem;
  color: #ffffff;
  box-shadow: 0 10px 15px -3px rgb(0 0 0 / 0.1), 0 4px 6px -4px rgb(0 0 0 / 0.1);
  outline: none !important;
  max-height: max-content;
  overflow: visible;
  --menu-origin-inline: calc(var(--popover-anchor-width) / 2);
}

:is(.dark .menu) {
  border-color: hsl(204 3% 26%);
  background-color: hsl(204 3% 18%);
  color: hsl(204 20% 100%);
  box-shadow: 0 10px 15px -3px rgb(0 0 0 / 0.25),
    0 4px 6px -4px rgb(0 0 0 / 0.1);
}

.menu-item {
  display: flex;
  cursor: default;
  scroll-margin: 0.5rem;
  align-items: center;
  gap: 0.5rem;
  border-radius: 0.25rem;
  padding: 0.5rem;
  outline: none !important;
  cursor: pointer;
}

.menu-item[aria-disabled="true"] {
  opacity: 0.25;
  color: hsl(204 10% 10% / 0.3);
}

.menu-item[data-active-item] {
  background-color: rgba(91, 93, 255, 1);
  color: hsl(204 20% 100%);
}

.menu-item:active,
.menu-item[data-active] {
  background-color: rgba(91, 93, 255, 1);
}

.menu[data-placement^="bottom"] {
  transform-origin: var(--menu-origin-inline) -11px;
}

.menu[data-placement^="top"] {
  transform-origin: var(--menu-origin-inline) calc(100% + 11px);
}

:is(.dark .menu-item[aria-disabled="true"]) {
  color: hsl(204 20% 100% / 0.3);
}

.button {
  @apply transition duration-300;
  display: flex;
  height: 2.5rem;
  touch-action: none;
  user-select: none;
  align-items: center;
  justify-content: center;
  gap: 0.25rem;
  white-space: nowrap;
  border-radius: 0.5rem;
  border-style: none;
  background-color: rgba(91, 93, 255, 1);
  padding-left: 1rem;
  padding-right: 1rem;
  font-size: 1rem;
  line-height: 1.5rem;
  color: hsl(204 20% 100%);
  text-decoration-line: none;
  outline-width: 2px;
  outline-offset: 2px;
  outline-color: rgba(91, 93, 255, 0.4);
}

.button:hover {
  background-color: rgba(91, 93, 255, 0.72);
  transform: scale(0.95);
}

.button[aria-disabled="true"] {
  opacity: 0.5;
}

.button[data-focus-visible] {
  outline-style: solid;
}

@media (min-width: 640px) {
  .button {
    gap: 0.5rem;
  }
}

a {
  @apply text-primary transition duration-300 font-bold;
}

.login-pf a:hover {
  @apply text-primary;
}
